 verantwortliche Organisation für Medizinische Fachangestellte auf Arbeitnehmerseite. Sie vertritt uns MFA bei Tarifverhandlungen gegenüber der Arbeitsgemeinschaft zur Regelung der Arbeitsbedingungen der ArzthelferInnen / Medizinischen Fachangestellten (AAA). Die AAA ist die Tarifpartei der Arbeitgeber, sprich der niedergelassenen Ärzte, mit der der Manteltarifvertrag verhandelt wird. ")]),_c('p',[_vm._v(" Die niedergelassenen Ärzte sind nicht automatisch Mitglied in der AAA. Deshalb kann man nicht zwingend davon ausgehen, dass sich Praxen an die Gehälter im Tarifvertrag halten müssen. Arbeitgeber sind nur dann dazu verpflichtet nach Gehaltstarifvertrag zu zahlen, wenn sowohl sie Mitglied im AAA als auch die MFA Mitglied im VMF sind. ")]),_c('p',[_vm._v(" Ehrlich gesagt, kenne ich nicht viele ArzthelferInnen, die Mitglied im VMF sind. Daher wundert es auch nicht, dass so viele von uns untertariflich verdienen, wie die Kollegin aus dem Forum im obigen Beispiel. ")]),_c('p',[_vm._v(" Dennoch gibt es viele Ärzte, die sich freiwillig an den aktuellen Tarifvertrag halten oder zumindest das Gehalt für ihre Medizinischen Fachangestellten daran anlehnen. ")]),_c('p',[_vm._v(" Neben dem offiziellen Tarifvertrag existieren zahlreiche Gehaltstarifverträge von privaten Unternehmen, Krankenhäusern, dem öffentlichen Dienst (z.B. wenn Du bei der Kassenärztlichen Vereinigung arbeitest) und kirchlichen Trägern (wie der Caritas, der Diakonie oder der Johanniter). ")]),_c('p',[_vm._v(" Es kommt im privaten Bereich oftmals zu einer außertariflichen Bezahlung. Das muss nicht unbedingt negativ für Dich ausfallen. Viele MFA werden dadurch übertariflich bezahlt. ")]),_c('p',[_vm._v(" Wenn dein Chef kein Mitglied im AAA ist, Du aber darauf bestehst, dass dein Gehalt nach Tarifvertrag bezahlt wird, kannst du alternativ mit deinem Arbeitgeber vereinbaren, dass er sich freiwillig daran hält. Hier ist es ratsam, eine entsprechende Klausel im Arbeitsvertrag aufnehmen zu lassen, die die Abmachung